{\n  list-style: none; }\n\nblockquote, q {\n  quotes: none; }\n\nblockquote {\n  &:before, &:after {\n    content: '';\n    content: none; } }\n\nq {\n  &:before, &:after {\n    content: '';\n    content: none; } }\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0; }","@font-face {\n  font-family: 'loreal-texg';\n  src: url('fonts/texgyreadventor-regular.eot'); /* IE9 Compat Modes */\n  src: url('fonts/texgyreadventor-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n       url('fonts/texgyreadventor-regular.woff') format('woff'), /* Modern Browsers */\n       url('fonts/texgyreadventor-regular.ttf')  format('truetype'), /* Safari, Android, iOS */\n       url('fonts/texgyreadventor-regular.svg#eb38d5b06bf3b7ce91bf1ab81bd95287') format('svg'); /* Legacy iOS */\n       \n  font-style:   normal;\n  font-weight:  400;\n}\n\n\n@font-face {\n  font-family: 'loreal-texg';\n  src: url('fonts/texgyreadventor-bold.eot'); /* IE9 Compat Modes */\n  src: url('fonts/texgyreadventor-bold.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n       url('fonts/texgyreadventor-bold.woff') format('woff'), /* Modern Browsers */\n       url('fonts/texgyreadventor-bold.ttf')  format('truetype'), /* Safari, Android, iOS */\n       url('fonts/texgyreadventor-bold.svg#410bc7c0a96055f314865205a0a58b43') format('svg'); /* Legacy iOS */\n       \n  font-style:   normal;\n  font-weight:  600;\n}\n\n","// Susy Box Sizing\n// =================\n\n// Global Box Sizing\n// -----------------\n// Set a box model globally on all elements.\n// - [$box]: border-box | content-box\n// - [$inherit]: true | false\n@mixin global-box-sizing(\n  $box: susy-get(global-box-sizing),\n  $inherit: false\n) {\n  $inspect: $box;\n\n  @if $inherit {\n    @at-root {\n       html { @include output((box-sizing: $box)); }\n       *, *:before, *:after { box-sizing: inherit; }\n     }\n  } @else {\n    *, *:before, *:after { @include output((box-sizing: $box)); }\n  }\n\n  @include susy-inspect(global-box-sizing, $inspect);\n  @include update-box-model($box);\n}\n\n// Border Box Sizing\n// -----------------\n// A legacy shortcut...\n// - [$inherit]: true | false\n@mixin border-box-sizing(\n  $inherit: false\n) {\n  @include global-box-sizing(border-box, $inherit);\n}\n\n// Update Box Model\n// ----------------\n// PRIVATE: Updates global box model setting\n@mixin update-box-model(\n  $box\n) {\n  @if $box != susy-get(global-box-sizing) {\n  @include susy-set(global-box-sizing, $box);\n  }\n}\n","@charset \"UTF-8\";\n\n/// A mixin for generating vendor prefixes on non-standardized properties.\n///\n/// @param {String} $property\n///   Property to prefix\n///\n/// @param {*} $value\n///   Value to use\n///\n/// @param {List} $prefixes\n///   Prefixes to define\n///\n/// @example scss - Usage\n///   .element {\n///     @include prefixer(border-radius, 10px, webkit ms spec);\n///   }\n///\n/// @example css - CSS Output\n///   .element {\n///     -webkit-border-radius: 10px;\n///     -moz-border-radius: 10px;\n///     border-radius: 10px;\n///   }\n///\n/// @require {variable} $prefix-for-webkit\n/// @require {variable} $prefix-for-mozilla\n/// @require {variable} $prefix-for-microsoft\n/// @require {variable} $prefix-for-opera\n/// @require {variable} $prefix-for-spec\n\n@mixin prefixer($property, $value, $prefixes) {\n  @each $prefix in $prefixes {\n    @if $prefix == webkit {\n      @if $prefix-for-webkit {\n        -webkit-#{$property}: $value;\n      }\n    } @else if $prefix == moz {\n      @if $prefix-for-mozilla {\n        -moz-#{$property}: $value;\n      }\n    } @else if $prefix == ms {\n      @if $prefix-for-microsoft {\n        -ms-#{$property}: $value;\n      }\n    } @else if $prefix == o {\n      @if $prefix-for-opera {\n        -o-#{$property}: $value;\n      }\n    } @else if $prefix == spec {\n      @if $prefix-for-spec {\n        #{$property}: $value;\n      }\n    } @else  {\n      @warn \"Unrecognized prefix: #{$prefix}\";\n    }\n  }\n}\n\n@mixin disable-prefix-for-all() {\n  $prefix-for-webkit:    false !global;\n  $prefix-for-mozilla:   false !global;\n  $prefix-for-microsoft: false !global;\n  $prefix-for-opera:     false !global;\n  $prefix-for-spec:      false !global;\n}\n","@import 'normalize';\n@import \"~susy/sass/susy\";\n@import 'bourbon/bourbon';\n\n@import 'fonts.scss';\n\n$susy: ( flow: ltr, columns: 12, gutters: .25, math: fluid, output: float, container-position: center, container: 80%, global-box-sizing: border-box, debug: ( image: hide, color: rgba(#66f, .25), output:background, toggle: top right));\n\n$map: layout(12 .25 inside);\n\n$medium-screen : 1000px;\n$small-screen : 730px;\n$mobile-screen : 505px;\n\n// Padding / Margin inside Box\n@include border-box-sizing;\n\nbody {\n    font-family:loreal-texg;\n}\n\nh1 {\n    font-size:1.8em;\n    font-weight:600;\n    line-height: 1.2em;\n    @media (max-width:$small-screen){\n        font-size:1.3em;\n    }\n\n}\n\nh3 {\n    font-size:1.2em;\n    font-weight:600;\n}\n\np {\n    line-height:1.6em;\n    @media (max-width:$small-screen){\n        font-size:0.8em;\n    }\n}\n\nmain {\n    height:100vh;\n    width:100%;\n    overflow:hidden;\n    background:#F4F2F3;\n    @include display(flex);\n    @include justify-content(center)\n    @include align-items(center)\n    @media (max-width:$small-screen){\n        overflow:auto;\n        padding-bottom:100px;\n    }\n}\n\n#loreal {\n    width:90%;\n    height:80%;\n    background:white;\n    position:relative;\n    @media (max-width:$small-screen){\n        height:100%;\n        width:100%;\n    }\n}\n\n.first,\n.second {\n    position:absolute;\n    section {\n        position:relative;\n    }\n}\n\n.first {\n    @include span(5)\n    height:100%;\n    background-image:url('http://loreal.goom.fr/images/sideBg.jpg');\n    background-size:cover;\n    section {\n        padding-left: 12%;\n        padding-top:8%;\n        @media (max-width:$small-screen){\n            padding-left:0%;\n            text-align:center;\n        }\n        img {\n            width:143px;\n            @media (max-width:$small-screen){\n                width:93px;\n            }\n        }\n    }\n    @media (max-width:$small-screen){\n        @include span(12)\n        // position:fixed;\n    }\n}\n\n.second {\n    height:100%;\n    width:100%;\n    @include display(flex)\n    @include align-items(center)\n    @include justify-content(center)\n    @include flex-direction(column)\n    @media (max-width:$small-screen){\n        height:auto;\n        padding-top:40px;\n    }\n}\n\n.center-first {\n    width:90%;\n    padding:20px 0;\n    > div {\n        &:last-of-type {\n            @include span(6)\n            padding-left:25px;\n            @include last;\n            @media (max-width:$small-screen){\n                @include span(12)\n                padding-top:10px;\n                padding-left:0px;\n                @include display(flex)\n                @include justify-content(space-around)\n            }\n            ul {\n                border-bottom:1px solid #F3F3F3;\n                li {\n                    display:inline-block;\n                    text-transform:uppercase;\n                    padding-bottom:10px;\n                    margin-right:50px;\n                    letter-spacing:0.15em;\n                    font-weight:600;\n                    cursor:pointer;\n                    font-size:0.9em;\n                    &.active {\n                        border-bottom:5px solid #F3F3F3;\n                    }\n                    @media (max-width:$small-screen){\n                        &:first-of-type {\n                            margin-right:20px;\n                        }\n                        &:last-of-type {\n                            margin-right:0px;\n                        }\n                        color:white;\n                    }\n                }\n            }\n        }\n    }\n}\n\n.center {\n    background:white;\n    width:90%;\n    @include display(flex)\n    @media (max-width:$small-screen){\n        @include flex-direction(column)\n    }\n    .left {\n        @include span(6)\n        @media (max-width:$small-screen){\n            @include span(12)\n            height:300px;\n        }\n        box-shadow: -11px 7px 14px 0px rgba(0,0,0,0.50);\n        height:500px;\n        > div {\n            &:first-of-type {\n                height:65%;\n                @media (max-width:$small-screen){\n                    height:55%;\n                }\n                background-size:cover;\n                background-position:50% 50%;\n                @include transition(background-image 0.5s ease-in-out)\n                @include transition-delay(0.2s)\n                @include transform(translate3d(0,0,0))\n            }\n            &:last-of-type {\n                height:35%;\n                @media (max-width:$small-screen){\n                    height:45%;\n                }\n                background:black;\n                color:white;\n                @include display(flex)\n                @include flex-direction(column)\n                @include justify-content(center)\n                \n                article {\n                    text-align:left;\n                    padding-left:5%;\n                    h3 {\n                        padding-bottom:7px;\n                    }\n                    @media (max-width:$small-screen){\n                        padding:6%;\n                    }\n                }\n            }\n        }\n    }\n    .right {\n        @include span(6)\n        @include last;\n        @media (max-width:$small-screen){\n            @include span(12)\n            padding:20px;\n        }\n        padding-left:25px;\n        background:white;\n        position:relative;\n        .published_date {\n            padding:15px 0;\n            opacity:0.4;\n            font-weight:600;\n        }\n        > p {\n            padding-bottom:40px;\n        }\n        .bottom_controls {\n            height:auto;\n            background:white;\n            width:100%;\n            position:absolute;\n            bottom:0;\n            @media (max-width:$small-screen){\n                position:relative;\n            }\n            > div {\n                &.player_controls {\n                    position: relative;\n                    width: 265px;\n                    height: 50px;\n                    bottom:20px;\n                    text-align: left;\n                    > div {\n                        &.controls {\n                            display: inline-block;\n                            padding: 6px 0px 0  0px;\n                            height: 100%;\n                            width: 43px;\n                            > img {\n                                position: absolute;\n                                cursor: pointer;\n                                &#play {\n                                    -webkit-transition: all 0.3s ease-in-out;\n                                    &:hover {\n                                        opacity: 0.5;\n                                        -webkit-transition: all 0.3s ease-in-out;\n                                    }\n                                }\n                                &#pause {\n                                    left: 0px;\n                                    display: none;\n                                    -webkit-transition: all 0.3s ease-in-out;\n                                    &:hover {\n                                        opacity: 0.5;\n                                        -webkit-transition: all 0.3s ease-in-out;\n                                    }\n                                }\n                                &#loading {\n                                    -webkit-animation: spin 1s linear infinite;\n                                    -moz-animation: spin 1s linear infinite;\n                                    animation: spin 1s linear infinite;\n                                    left: 5px;\n                                    bottom: 14px;\n                                    display: none;\n                                }\n                            }\n                        }\n                        &.share {\n                            display: inline-block;\n                            padding: 0 10px;\n                            position: relative;\n                            bottom: 17px;\n                        }\n                        &.volume {\n                            display: inline-block;\n                            padding: 0 10px;\n                            position: relative;\n                            bottom: 15px;\n                            cursor: pointer;\n                            &:hover {\n                                .volume_slider {\n                                    input {\n                                        width: 64px;\n                                        -webkit-transition: width 0.4s ease-in-out;\n                                        transition: width 0.4s ease-in-out;\n                                    }\n                                }\n                                input[type=range]::-webkit-slider-thumb {\n                                    opacity: 1;\n                                    -webkit-transition: opacity 0.4s ease-in-out;\n                                    transition: opacity 0.4s ease-in-out;\n                                }\n                                input[type=range]::-moz-range-thumb {\n                                    opacity: 1;\n                                    transition: opacity 0.4s ease-in-out;\n                                }\n                            }\n                            > img {\n                                display: inline-block;\n                                width: 19px;\n                                &.muted {\n                                    width: 23px;\n                                }\n                            }\n                            > div {\n                                &.volume_icon {\n                                    width: 20px;\n                                    height: 20px;\n                                    background-image: url('../images/volume_icn.png');\n                                    background-repeat: no-repeat;\n                                    background-size: contain;\n                                    position: relative;\n                                    bottom: -14px;\n                                    left: -3px;\n                                    -webkit-touch-callout: none;\n                                    -webkit-user-select: none;\n                                    -khtml-user-select: none;\n                                    -moz-user-select: none;\n                                    -ms-user-select: none;\n                                    user-select: none;\n                                    &.muted {\n                                        background-image: url('../images/volume_mute_icn.png');\n                                        width: 24px;\n                                    }\n                                }\n                                &.volume_slider {\n                                    display: inline-block;\n                                    position: relative;\n                                    left: 26px;\n                                    bottom: 9px;\n                                    cursor: pointer;\n                                    input {\n                                        width: 0px;\n                                        -webkit-transition: width 0.4s ease-in-out;\n                                        transition: width 0.4s ease-in-out;\n                                    }\n                                }\n                      